 }\n\n  hr.canopy-footnote-rule {\n    width: 700px;\n    display: block;\n    height: 1px;\n    border: 0;\n    border-top: 1px solid #ccc;\n    margin: 12px 0;\n    padding: 0;\n  }\n\n  sup {\n    font-size: 60%;\n    padding: 2px;\n  }\n\n  .canopy-footnote-span {\n    font-size: 80%;\n  }\n\n  blockquote {\n    padding: 0 .7em;\n    border-left: 0.25em solid lightgray;\n    color: #565e67;\n\n    margin-block-start: .5em;\n    margin-block-end: .5em;\n    margin-inline-start: 0px;\n    margin-inline-end: 0px;\n\n    a.canopy-selectable-link {\n      text-decoration: underline #D3D3D3;\n    }\n  }\n\n  pre {\n    margin: 10px 10px;\n    background-color: #eff0f1;\n    padding: 5px;\n    font-size: 85%;\n  }\n\n  code {\n    background-color: #eff0f1;\n    font-size: 90%;\n  }\n\n  table {\n    margin: 7px auto;\n    table-layout: fixed;\n    width: 70%;\n    border-collapse: collapse;\n    border: 2px solid black;\n\n    td, th {\n      border: 2px solid black;\n      text-align: center;\n      padding: 6px;\n    }\n  }\n\n  .canopy-image {\n    width: 220px;\n    max-height: 250px;\n  }\n\n  div.canopy-image-div {\n    float: right;\n    border: 1px solid #c8ccd1;\n    padding: 3px;\n    background-color: #f8f9fa;\n    font-size: 94%;\n    text-align: center;\n    overflow: hidden;\n    margin: 10px;\n  }\n\n  div.canopy-image-div > sup {\n    display: block;\n  }\n\n  .canopy-url-link-span > svg {\n    height: 15px;\n    width: 12px;\n    display: none;\n  }\n\n  .canopy-selected-link + svg, .canopy-url-link-span:hover > svg {\n    display: inline-block;\n    vertical-align: middle;\n  }\n\n  ol, ul {\n    margin: 7px 10px;\n  }\n\n  div.canopy-raw-html {\n    display: inline;\n  }\n}\n\n#_canopy > section.canopy-topic-section {\n  padding-top: 30px;\n}\n"],"sourceRoot":""}]);
